.footer {
    @include respond-to("laptop") {
        background-position-x: 80%;

        &__address,
        &__info,
        &__services {
            width: 33.333%;
        }
    }


    @include respond-to("iphone") {
        text-align: center;
        background-position-x: 80%;

        &__logo {
            margin: 0;
        }


        &__address {
            width: auto;
        }

        div &__time {
            font-size: 14px;
            font-weight: 800;
            line-height: 200%;
            opacity: 0.5;
        }

        &__info {
            width: auto;
            text-align: center;
            margin-bottom: 20px;
        }

        &__case {
            flex-direction: column-reverse;
            margin-bottom: 0;
        }

        &__services {
            display: none;
        }

        &__social {
            margin: 30px 0;
        }
    }

    background: #7E8780 url('/img/footer/footer.png') no-repeat top right;
    color: $white;

    h3 {
        margin-top: 0;

        @include respond-to("iphone") {
            font-size: 20px;
            margin: 0 0 10px 0;
        }

        font-size: 25px;
        line-height: 200%;
    }

    &__logo {
        display: inline-block;
        width: 186px;
        height: 53px;
        border: 1px dashed red;
        margin-bottom: 40px;
    }

    &__case {
        display: flex;
        margin-bottom: 100px;
    }

    &__address {
        width: 25%;

        div {
            font-size: 20px;
            line-height: 150%;
        }
    }

    div &__time {
        margin: 30px 0 0;
        line-height: 200%;
    }

    &__phone {
        margin: 40px 0;

        a {
            display: block;
            font-size: 25px;
            line-height: 150%;
            color: $white;
            text-decoration: none;

            &:hover {
                text-decoration: underline;
            }
        }
    }

    &__social {
        margin: 40px 0;
        a {
            display: inline-block;
            margin-right: 20px;
            width: 50px;
            height: 50px;
            border: 1px dashed red;
            border-radius: 50%;
        }
    }

    &__info {
        width: 25%;
    }

    &__services {
        width: 50%;
    }

    &__menu {

    }

    &__link {
        display: block;
        text-decoration: none;
        font-size: 20px;
        font-weight: 300;
        line-height: 200%;
        color: $white;

        &:hover {
            text-decoration: underline;
        }
    }

    &__about {
        @include respond-to("iphone") {
            display: block;

            &Item {
                font-size: 12px;
                line-height: 200%;
            }
        }

        display: flex;
        justify-content: space-between;

        &Item {
            min-height: 36px;
            font-size: 16px;
            font-weight: 800;
            opacity: 0.5;

            &:hover {
                opacity: 1;
            }
        }
    }

    &__blaze {
        display: inline-block;
        position: relative;
        padding-right: 70px;

        &Link {
            position: absolute;
            display: block;
            width: 54px;
            height: 36px;
            top: -25%;
            right: 0;
            border: 1px dashed red;
        }
    }
}

