.catalogFilter {
    list-style: none;
    margin-bottom: 20px;

    &__item {
        @include respond-to("laptop") {
            display: inline-block;
            margin-right: 10px;
        }

        @include respond-to("iphone") {
            display: inline-block;
            margin-right: 6px;
            margin-bottom: 4px;
        }

        margin-bottom: 10px;
    }

    &__title {
        @include respond-to("laptop") {
            font-size: 14px;
        }

        @include respond-to("iphone") {
            font-size: 12px;
            padding: 4px 14px;
        }

        display: inline-block;
        padding: 5px 20px;
        color: $text;
        border-radius: 30px;
        text-decoration: none;
        font-size: 20px;
        background: #f5f5f5;
        line-height: 150%;

        &:hover {
            background: $green;
            color: $white;
        }

        &__active {
            border: 2px solid $green;
            color: $green;
            background: none;
        }
    }
}