.catalog {
    @include respond-to("laptop") {
        display: block;
    }


    display: flex;

    color: #000;
    font-size: 16px;
    font-weight: 200;
    line-height: 200%;

    &__left {
        @include respond-to("laptop") {
            display: none;
        }

        min-width: 270px;
        margin-right: 40px;
    }

    &__right {
        @include respond-to("laptop") {
            order: 5;
            margin: 0;
        }

        order: 20;
        min-width: 270px;
        margin-left: 40px;
    }

    &__main {
        order: 10;
        width: auto;
    }
}