.catalogPrice {
    &__item {
        margin-bottom: 3px;
    }

    &__header {
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
        padding: 8px 20px;
        background: #F8F8F8;

        .ui-accordion-header-icon {
            display: none;
        }
    }

    &__title {
        width: 80%;
    }

    &__marker {
        @include respond-to("iphone") {
            display: none;
        }

        width: 24px;
        height: 24px;
        border: 1px solid #f00;
        border-radius: 50%;
    }

    &__description {
        padding: 30px 20px;
        background: #F4F4F4;

        p {
            margin: 0 0 30px 0;
        }
    }
}