.catalogMenu {
    list-style: none;

    li:first-child {
        .catalogMenu__title {
            text-decoration: underline;
        }

        ul {
            display: block;
        }
    }

    &__title {
        display: block;
        margin-bottom: 20px;
        font-size: 20px;
        font-weight: 800;
        line-height: 110%;
        color: $text;
        text-decoration: none;
        max-width: 180px;
    }

    ul {
        display: none;
        margin: 0;
        padding: 10px 20px 10px 20px;
    }

    &__link {
        display: block;
        margin-bottom: 16px;
        text-decoration: none;
        font-size: 16px;
        line-height: 120%;
        color: $green;
        max-width: 160px;
    }
}