.articlesList {
    &__item {
        display: block;
        color: $black;
        text-decoration: none;
        clear: both;
        margin-bottom: 20px;
    }

    &__photo {
        @include respond-to("iphone") {
            width: 100%;
        }


        float: left;
        margin-right: 30px;
        margin-bottom: 10px;
    }

    &__title {
        font-size: 20px;
        font-weight: 800;
        line-height: 200%;
    }

    &__description {
        line-height: 200%;
    }
}