.actions {
    &__photo {
        @include respond-to("iphone") {
            width: 100%;
            margin: 0;
            float: none;
        }

        float: right;
        margin: 0 0 40px 40px;
    }

    &__duration {
        font-size: 16px;
        font-weight: 300;
        color: $text;
    }
}


.actionsList {
    @include respond-to("laptop") {
        &__item {
            width: 33.33%;
        }
    }


    @include respond-to("ipad") {
        &__item {
            width: 50%;
        }
    }


    @include respond-to("iphone") {
        display: block;

        &__item {
            width: auto;
        }
    }


    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;

    &__item {
        position: relative;
        width: 25%;
        margin-bottom: 20px;
    }

    &__link {
        display: block;
        padding: 0 15px;
        text-decoration: none;
    }

    &__photo {
        img {
            width: 100%;
        }
    }

    &__duration {
        position: absolute;
        top: 15px;
        left: 30px;
        background: $green;
        color: $white;
        font-size: 12px;
        line-height: 150%;
        padding: 4px 12px;
    }

    &__title {
        padding: 20px;
        color: $black;
        font-size: 16px;
        line-height: 150%;
    }
}