.forms {
    position: relative;
    display: none;


    input {
        outline: none;
    }

    &__case {
        box-sizing: border-box;
        max-width: 380px;
        padding: 40px;
        background: $white;
    }

    &__close {
        position: absolute;
        top: 20px;
        right: 20px;
        width: 20px;
        height: 20px;

        svg {
            fill: #C2C2C2;

            &:hover {
                fill: $black;
            }
        }
    }

    a {
        color: $green;
        text-decoration: none;

        &:hover {
            text-decoration: underline;
        }
    }

    &__title {
        margin-top: 0;
        font-size: 18px;
        color: $black;
        font-weight: 800;
        line-height: 150%;
    }

    &__group {
        margin-bottom: 20px;
    }

    &__label {
        font-size: 12px;
        line-height: 200%;
        font-weight: 300;
    }

    &__text {
        border: 1px solid #979797;
        width: 100%;
        box-sizing: border-box;
        padding: 12px 15px;
    }

    &__file {
        padding-top: 10px;
        padding-bottom: 7px;

        &Input {
            display: none;
        }

        &Label {
            border: 1px solid #979797;
            border-radius: 5px;
            padding: 8px 15px;
            cursor: pointer;

            &:hover {
                background: $green;
                color: $white;
                border-color: $green;
            }
        }

        &Icon {
            margin-left: 10px;
        }
    }

    &__agreement {
        display: flex;

        input {
            position: relative;
            top: 5px;
            margin-right: 10px;
        }
    }

    &__btn {
        box-sizing: border-box;
        width: 100%;
    }
}