.reviewsShort {

    @include respond-to("iphone") {
        display: block;

        &__item {
            margin-bottom: 15px;
        }
    }
    display: flex;
    align-items: stretch;
    margin: 0 -25px 40px -25px;

    &__item {
        box-sizing: border-box;
        height: 100%;
        margin: 0 25px;
        border: 1px solid #c4c4c4;
        padding: 30px;
        border-radius: 5px;
    }

    &__photo {
        border-radius: 50%;
        float: left;
        margin: 0 20px 20px 0;
    }

    &__title {
        font-size: 20px;
        line-height: 200%;
    }

    &__date {
        display: inline-block;
        border-top: 1px solid #8c8c8c;
        font-size: 14px;
        line-height: 200%;
        font-weight: 300;
        color: #8c8c8c;
    }

    &__text {
        clear: both;
        font-family: Muli, sans-serif;
        font-style: italic;
        font-size: 16px;
        line-height: 180%;
        color: #8c8c8c;
    }
}

.reviewsList {
    &__item {
        box-sizing: border-box;
        position: relative;
        height: 100%;
        border: 1px solid #c4c4c4;
        padding: 30px;
        margin-bottom: 40px;
        border-radius: 5px;
    }

    &__photo {
        border-radius: 50%;
        float: left;
        margin: 0 20px 10px 0;
    }

    &__title {
        font-size: 20px;
        line-height: 200%;
    }

    &__date {
        display: inline-block;
        border-top: 1px solid #8c8c8c;
        font-size: 14px;
        line-height: 200%;
        font-weight: 300;
        color: #8c8c8c;
    }

    &__original {
        position: absolute;
        top: 15px;
        right: 20px;
        color: $green;
    }

    &__text {
        clear: both;
        font-family: Muli, sans-serif;
        font-style: italic;
        font-size: 16px;
        line-height: 180%;
        font-weight: 200;
    }

    &__btn {
        text-align: center;
        margin-bottom: 40px;
    }
}