.doctorsList {
    @include respond-to("ipad") {
        &__item {
            width: 50%;
        }
    }


    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;

    &__item {
        width: 33.333%;
    }

    &__link {
        @include respond-to("iphone") {
            margin: 0 10px 20px 10px;
        }


        display: block;
        margin: 0 15px 30px 15px;
        text-align: center;
        text-decoration: none;
        color: $black;
    }

    &__photo {
        width: 100%;
    }

    &__title {
        font-size: 18px;
        font-weight: 800;
    }
}


.doctorsDetail {
    @include respond-to("ipad") {
        display: block;

        &__photo {
            width: 100%;
        }
    }

    display: flex;
    align-items: flex-start;

    &__photo {
        margin: 0 30px 30px 0;
    }

    &__position {
        font-size: 20px;
        font-weight: 800;
        line-height: 200%;
        color: $text;
    }

    &__experience {
        font-size: 20px;
        font-weight: 800;
        line-height: 200%;
        color: $text;
    }

    &__btn {
        margin: 30px 0;
    }
}