.doctorsSlider {
    padding-bottom: 20px;

    &__item {
        text-align: center;
        padding: 0 15px;
    }


    &__photo {
        width: 100%;
        margin: 0 auto;
    }

    &__title {
        font-size: 18px;
        font-weight: 800;
        text-align: center;
        line-height: 200%;
    }

    &__position {
        font-size: 16px;
        text-align: center;
    }
}