@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.11.2/css/all.min.css);
@font-face {
    font-family: 'Muli';
    src: local('Muli Light'), local('Muli-Light'),
        url('/fonts/muli/Mulilight.woff2') format('woff2'),
        url('/fonts/muli/Mulilight.woff') format('woff'),
        url('/fonts/muli/Mulilight.ttf') format('truetype');
    font-weight: 300;
    font-style: normal;
}

