.servicesMenu {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    margin: 0 -15px;
    padding: 0;

    @include respond-to("iphone") {
        display: block;
    }

    li {
        width: 33.33%;

        @include respond-to("laptop") {
            width: 50%;
        }

        @include respond-to("iphone") {
            width: auto;
        }
    }

    &__item {
        position: relative;
        background: #fff;
        min-height: 310px;
        margin: 0 15px 30px 15px;
        padding: 20px 30px;
        border-radius: 5px;

        @include respond-to("iphone") {
            min-height: auto;
            margin-bottom: 15px;
            padding: 15px;
        }
    }

    &__title {
        display: block;
        text-decoration: none;
        font-size: 20px;
        font-weight: 800;
        text-transform: uppercase;
        line-height: 150%;
        color: $text;
        margin-bottom: 10px;
    }


    &__subMenu {
        @include respond-to("iphone") {
            display: none;
            margin-top: 3px;
        }
    }

    &__link {
        display: block;
        font-size: 18px;
        line-height: 200%;
        text-decoration: none;
        color: $green;
    }


    &__linkAll {
        position: absolute;
        bottom: 24px;
        left: 30px;
        font-size: 14px;
        font-weight: 800;
        color: $text;
        text-decoration: none;

        @include respond-to("iphone") {
            display: none;
        }
    }
}