.header {
    z-index: 1000;

    @include respond-to("laptop") {
        position: fixed;
        top: 0;
        right: 0;
        left: 0;
        background: $white;
    }

    border-bottom: 1px solid #F5F5F5;

    &__top {
        background: #F5F5F5;

        @include respond-to("laptop") {
            display: none;
        }
    }


    &__mobileMenu {
        display: none;

        .servicesMenu {
            &__item {
                border-top: 1px solid #f5f5f5;
                margin-bottom: 0;
            }
        }


        &Icon {
            @include respond-to("laptop") {
                display: block;
            }

            @include respond-to("iphone") {
                margin-right: 10px;
            }





            display: none;
            min-width: 51px;
            height: 28px;
            border: #ff0000 1px solid;
            border-radius: 14px;
            margin-right: 20px;
        }
    }


    &__mainMenu {
        &Case {
            display: flex;
            align-content: center;
            align-items: center;
            justify-content: space-between;
            height: 50px;
        }

        &Link {
            text-decoration: none;
            color: $text;
            margin-right: 40px;

            &:hover {
                color: $black;
                text-decoration: underline;
            }
        }
    }

    &__address {

    }

    &__center {
        @include respond-to("laptop") {
            padding-top: 15px;
            padding-bottom: 15px;
        }

        @include respond-to("iphone") {
            padding-top: 15px;
            padding-bottom: 15px;
        }

        display: flex;
        flex-wrap: nowrap;
        align-content: center;
        align-items: center;
        padding-top: 30px;
        padding-bottom: 30px;
    }

    &__logo {
        @include respond-to("iphone") {
            width: 85px;
            height: 24px;
        }

        display: block;
        width: 186px;
        height: 53px;
        border: 1px dashed red;

        &Case {
            width: 80%;
        }

        img {
            width: 100%;
        }
    }

    &__phone {
        @include respond-to("laptop") {
            font-size: 16px;
        }

        font-size: 20px;
        white-space: nowrap;

        &:hover {
            color: $black;
            text-decoration: underline;
        }
    }

    &__search {
        @include respond-to("iphone") {
            display: none;
        }


        &Btn {
            @include respond-to("laptop") {
                margin: 0 0 0 20px;
            }

            width: 50px;
            height: 50px;
            margin: 0 30px;
            border: 1px dashed red;
            border-radius: 25px;
            background: #E5E5E5;
        }
    }

    &__btn {

        @include respond-to("laptop") {
            display: none;
        }
    }

    &__services {
        &Menu {
            &Case {
                @include respond-to("laptop") {
                    display: none;
                }
            }

            display: flex;
            list-style: none;
            flex-wrap: nowrap;
            align-content: flex-start;
            align-items: center;
            justify-content: space-between;

            &Item {
                &:hover .header__servicesSubMenu {
                    display: block;
                    position: absolute;
                }
            }

            &Link {
                position: relative;
                display: block;
                padding: 0 5px 20px 5px;
                text-align: center;
                text-decoration: none;
                color: $text;

                &:hover {
                    color: $black;
                    text-decoration: underline;
                }
            }
        }

        &SubMenu {
            display: none;
            list-style: none;
            border: 1px solid #F3F3F3;
            padding: 20px;
            background: $white;
            z-index: 100;

            &:hover {
                display: block;
            }

            &Link {
                display: block;
                font-size: 16px;
                font-weight: 300;
                line-height: 150%;
                margin-bottom: 10px;
                text-decoration: none;
                color: $green;

                &:hover {
                    text-decoration: underline;
                }
            }
        }
    }
}