.mainSliders {
    padding: 60px 0;

    &__title {
        font-size: 36px;
        font-weight: 800;
        line-height: 150%;
    }

    &__item {
        display: flex;
        align-content: center;
        align-items: center;
        justify-content: space-between;
    }

    &__photo {
        border-radius: 50%;
    }
}