$text: #595959;
$green: #89AE96;
$white: #fff;
$black: #000;


h1, h2, h3, h4, h5, h6, ul {
    margin: 0;
    padding: 0;
}

h1 {
    margin: 30px 0;
    font-size: 36px;
    font-weight: 800;
    line-height: 200%;
    color: $green;
}

h2 {
    font-size: 36px;
    font-weight: 800;
    line-height: 150%;
    color: $text;
    margin-bottom: 26px;
}

h3 {
    font-size: 25px;
    font-weight: 800;
    line-height: 200%;
    margin: 20px 0;
}

body {
    @include respond-to("laptop") {
        padding-top: 85px;
    }

    @include respond-to("iphone") {
        padding-top: 60px;
    }


    font-family: "SF UI Display";
    font-size: 14px;
    line-height: 17px;
    color: $text;
}

.case {
    max-width: 1170px;
    margin: 0 auto;
    padding: 0 40px;

    @include respond-to("iphone") {
        padding: 0 15px;
    }
}

.page {
    @include respond-to("laptop") {
        margin-bottom: 80px;
    }

    @include respond-to("iphone") {
        margin-bottom: 40px;
    }

    margin-bottom: 100px;
}

.case__center {
    text-align: center;
}

.box {
    padding-top: 60px;
    padding-bottom: 60px;
}

.box-content {
    margin-bottom: 40px;
}

.box-image {
    @include respond-to("iphone") {
        display: block;


        img {
            width: 100%;
        }
    }

    display: flex;
    flex-direction: row-reverse;
    align-items: flex-start;

    p {
        margin: 0;
        margin-right: 30px;
    }
}

.bg_grey {
    background: #F5F5F5;
}

.bg_green {
    background: #C8E4D2;
}

.m_center {
    margin: 0 auto;
}


.breadcrumb {
    margin: 20px 0;

    &__link {
        color: $text;
        font-size: 14px;
        text-decoration: none;
    }

    &__separator {
        padding: 0 10px;
    }

    &__current {
        color: #b5b5b5;
    }
}

.article {
    &__menu {
        list-style: none;
        display: flex;
        flex-wrap: wrap;
        &Item {
            width: 50%;
            max-width: 400px;
        }

        &Link {
            display: inline;
            color: $green;
        }
    }

    a {
        color: $green;
        text-decoration: none;

        :hover {
            text-decoration: underline;
        }
    }
}

article {
    color: $black;

    ul {
        padding: 0 0 0 40px;
        list-style-image: url("/img/svg/catalog/list-image-tick.svg");
    }

    li {
        padding-left: 10px;
        margin-bottom: 20px;
        line-height: 150%;
    }

    p {
        max-width: 860px;
        line-height: 200%;
        margin-bottom: 30px;
    }
}

.photos {
    @include respond-to("iphone") {
        &__item {
            width: 50%;
            padding: 10px;
        }
    }

    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;
    line-height: 100%;

    &__item {
        width: 33.33%;
        padding: 0 15px 30px 15px;
        box-sizing: border-box;

        &__4 {
            width: 25%;

        }
    }

    img {
        width: 100%;
    }
}

.container {
    @include container();
    @include sm(max-width, rem(510));
    @include md(max-width, rem(690));
    @include lg(max-width, rem(930));
    @include xl(max-width, rem(1110));
}

.btn {
    display: inline-block;
    background: $green;
    border: 3px solid $green;
    padding: 13px 34px;
    color: $white;
    font-size: 16px;
    font-weight: 800;
    text-decoration: none;
    border-radius: 40px;
    white-space: nowrap;
    outline: none;

    &_white {
        background: none;
        color: $white;
        border: 3px solid $white;
    }

    &_green {
        background: none;
        color: $green;
        border: 3px solid $green;
    }
}

.tabs {
    &Slider {
        max-width: 860px;
    }

    ul {
        padding: 0;
        border-bottom: 2px solid #F3F3F3;
        margin-bottom: 30px;

        li {
            display: inline-block;
            margin: 0;
            padding-left: 0;
            list-style: none;

            &.ui-tabs-active {
                background: #F3F3F3;
            }
        }

        a {
            display: inline-block;
            padding: 10px 24px;
            font-size: 20px;
            line-height: 150%;
            text-decoration: none;
            color: $black;
            white-space: nowrap;
            outline: none;

            &:hover {
                background: $green;
            }
        }
    }
}

.pagination {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 80px;

    &__item {
        font-size: 14px;
        line-height: 100%;
        color: $black;
        margin: 0 8px;
        padding: 3px 6px;

        &__active {
            border-bottom: 1px solid $black;
        }
    }

    &__arrows {
        width: 30px;
        height: 30px;
        margin: 0 10px;
        border: 1px solid #ff0000;
        border-radius: 50%;
        background: #E5E5E5;
    }
}

.slick-prev {
    font-size: 0;
    background: url("/img/svg/slick/arr-prev.svg") center center no-repeat;
    border: none;
    outline: none;
    display: block;
    position: absolute;
    top: 50%;
    left: -80px;
    margin-top: -40px;
    width: 40px;
    height: 40px;
}

.slick-next {
    font-size: 0;
    background: url("/img/svg/slick/arr-next.svg") center center no-repeat;
    border: none;
    display: block;
    position: absolute;
    top: 50%;
    right: -80px;
    margin-top: -40px;
    width: 40px;
    height: 40px;
}

.slick-dots {
    display: flex;
    justify-content: center;
}

.slick-dots li {
    font-size: 0;
    margin: 0 7px;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background: #E5E5E5;
}

.slick-dots li.slick-active {
    background: $green;
}

.slick-dots li button {
    display: none;
}


.left {
    float: left;
}

.right {
    float: right;
}

.clearfix:after {
    content: "";
    display: table;
    clear: both;
}